import React from "react";
import { Accordion, AccordionTab } from "primereact/accordion";

const About = () => {
  return (
    <div style={{ padding: "20px" }}>
      <h1>About Me</h1>
      <Accordion>
        <AccordionTab header="Skills">
          <p>HTML, CSS, JavaScript, React, AdonisJS, Node.js.</p>
        </AccordionTab>
        <AccordionTab header="Experience">
          <p>2+ years in web development with projects in gaming and portfolio sites.</p>
        </AccordionTab>
        <AccordionTab header="Education">
          <p>Self-taught web developer with a focus on modern frontend and backend technologies.</p>
        </AccordionTab>
      </Accordion>
    </div>
  );
};

export default About;
