import React from "react";
import { Card } from "primereact/card";

const Home = () => {
  return (
    <div style={{ padding: "20px" }}>
      <Card title="Welcome to My Portfolio" style={{ textAlign: "center" }}>
        <p>
          I’m a passionate developer who loves creating functional and visually
          appealing projects. Explore my work and get to know me better!
        </p>
      </Card>
    </div>
  );
};

export default Home;
