import React from "react";
import { Card } from "primereact/card";

const Projects = () => {
  const projects = [
    { title: "Snake Game", description: "A classic snake game using JavaScript." },
    { title: "Tic Tac Toe", description: "A simple Tic Tac Toe game in React." },
    { title: "Portfolio", description: "My personal portfolio website." },
  ];

  return (
    <div className="p-grid" style={{ padding: "20px" }}>
      {projects.map((project, index) => (
        <div key={index} className="p-col-12 p-md-4">
          <Card title={project.title}>
            <p>{project.description}</p>
          </Card>
        </div>
      ))}
    </div>
  );
};

export default Projects;
